<script lang="ts">
import AspectRatio from '@monogrid/vue-boost/core/AspectRatio.vue'
import MarkdownBlock from '@monogrid/vue-boost/core/MarkdownBlock.vue'
import { gsap } from 'gsap'
import { ComponentPublicInstance, computed, defineComponent, inject, onUnmounted, ref, watch } from 'vue'

import { loaderInjectionKey } from '@/App.vue'
import CtaIcon from '@/components/atoms/CtaIcon.vue'
import HeroLayout from '@/components/atoms/HeroLayout.vue'
import IconComponent from '@/components/atoms/IconComponent.vue'
import LayerBgPlane from '@/components/atoms/LayerBgPlane.vue'
import LayerVideo from '@/components/atoms/LayerVideo.vue'
import OverlayTexts from '@/components/atoms/OverlayTexts.vue'
import VideoLauncher from '@/components/molecules/VideoLauncher.vue'
import ProjectsWall from '@/components/organisms/ProjectsWall.vue'
import SectionAboutUs from '@/components/organisms/sections/home/SectionAboutUs.vue'
import SectionOurOffices from '@/components/organisms/sections/home/SectionOurOffices.vue'
import SiteFooter from '@/components/organisms/SiteFooter.vue'
import { useGlobalReducedMotion } from '@/functions/useGlobalReducedMotion'
import { getSeoImageUrl, useSeo } from '@/functions/useHead'
import { useQueryVariables } from '@/functions/useQueryVariables'
import type { MediaFile } from '@/graphql' // eslint-disable-line
import { useGetHomePageQuery } from '@/graphql'
import { animateColorText } from '@/utils/animations'

export default defineComponent({
  name: 'HomePage',
  components: {
    AspectRatio,
    CtaIcon,
    HeroLayout,
    IconComponent,
    LayerBgPlane,
    LayerVideo,
    MarkdownBlock,
    OverlayTexts,
    ProjectsWall,
    SectionAboutUs,
    SectionOurOffices,
    SiteFooter,
    VideoLauncher
  },
  setup (props, { attrs, slots, emit }) {
    const loader = inject(loaderInjectionKey)
    const { data } = useGetHomePageQuery({ variables: useQueryVariables() })
    const reducedMotion = useGlobalReducedMotion()
    const hero = ref<ComponentPublicInstance<typeof HeroLayout>>()
    const whatWeDo = ref()
    const whatWeDoBgProgress = ref(0)
    const whatWeDoVideoMask = ref(1)
    const whatWeDoVideoOpacity = ref(0)

    const tls = []

    const content = computed(() => data.value?.content)

    watch([whatWeDo, reducedMotion], ([el, _reducedMotion]) => {
      if (!el || _reducedMotion) return
      const intro = gsap.utils.toArray('.intro', el)[0] as HTMLElement
      const video = gsap.utils.toArray('.video', el)[0] as HTMLElement
      const title = gsap.utils.toArray('h2', el)[0] as HTMLElement
      const cta = gsap.utils.toArray('.cta', el)[0] as HTMLElement
      const tl = gsap.timeline({
        scrollTrigger: {
          trigger: el,
          start: 'top top',
          end: 'bottom bottom',
          scrub: true
        }
      })

      tl.fromTo(whatWeDoBgProgress, { value: 0 }, { value: 1, duration: 0.6 })
      tl.fromTo(intro, { autoAlpha: 1 }, { autoAlpha: 0, duration: 0.3 })
      tl.fromTo(whatWeDoVideoMask, { value: 1 }, { value: 0, duration: 0.5 }, 'video-=0.05')
      tl.fromTo(whatWeDoVideoOpacity, { value: 0 }, { value: 1, duration: 0.5 }, 'video+=0.05')
      tl.from(video, { autoAlpha: 0, duration: 0.2 }, 'video+=0.35')
      tl.from({}, { duration: 0.2 }) // empty to elongate timeline

      tls.push(tl)

      const tl2 = gsap.timeline({
        scrollTrigger: {
          trigger: intro,
          start: 'top center',
          end: 'bottom center',
          scrub: true
        }
      })
      tl2.add(animateColorText(title, { duration: 0.3 }))
      tl2.fromTo(cta, { autoAlpha: 0 }, { autoAlpha: 1, duration: 0.05 }, 0.28)

      tls.push(tl2)
    })

    watch([content, loader], ([data, loaderInstance]) => {
      if (!data || !loaderInstance) return
      loaderInstance.hide()
    }, { immediate: true })

    onUnmounted(() => tls.forEach(tl => tl.kill()))

    useSeo({
      title: () => content.value?.seoTitle || content.value?.title,
      description: () => content.value?.seoDescription,
      ogImage: () => getSeoImageUrl(content.value?.seoImage)
    })

    return {
      content,
      hero,
      whatWeDo,
      whatWeDoBgProgress,
      whatWeDoVideoMask,
      whatWeDoVideoOpacity
    }
  }
})
</script>

<template>
  <div v-if="content">
    <HeroLayout ref="hero">
      <template #sticky="{progress}">
        <LayerVideo
          v-if="content.heroVideo"
          class="w-full h-full"
          cover
          dithered
          :scale="progress"
          :data="content.heroVideo"
        />
      </template>
      <div class="container pt-safe-header relative pb-4 md:pb-10 h-full w-full flex flex-col bg-half-gradient">
        <OverlayTexts
          :list="content.heroVideoOverlayLabels"
          :delay="3"
          class="w-full flex-auto"
        />
        <div v-reveal.group class="flex lt-lg:flex-col lg:items-end gap-4">
          <div class="flex-auto">
            <h1 v-reveal.child class="fluid-title">{{ content.heroText }}</h1>
          </div>
          <div class="tip">
            <div
              v-reveal.child="'fadeIn'"
              class="lg:text-right whitespace-nowrap"
            >
              <span>{{ $t('Scroll down') }}</span>
              <IconComponent class="ml-2 w-3 h-3" icon="segment" />
            </div>
          </div>
        </div>
      </div>
    </HeroLayout>
    <ProjectsWall
      class="-mt-sview"
      :transparent="true"
      :order="3"
      :title="content.featuredWorksTitle"
      :list="content.featuredWorksList"
    />
    <section ref="whatWeDo" class="what-we-do">
      <LayerBgPlane
        class="sticky top-0 motion-reduce:hidden h-lview w-full -mb-lview z-2"
        :order="2"
        :color-mix="whatWeDoBgProgress"
        :mask="whatWeDoVideoMask"
        :masked="true"
        :transparent="true"
        :square-size="3"
      />
      <div class="intro container z-3 motion-safe:-mt-lview sticky motion-reduce:relative motion-safe:top-0 w-full h-lview flex items-center justify-center">
        <LayerBgPlane
          class="absolute hidden md:block motion-reduce:hidden inset-0 w-full"
          :order="0"
          :color-mix="0"
          :transparent="true"
        />
        <div class="relative xl:max-w-[70%] text-center">
          <MarkdownBlock
            tag="h2"
            inline
            class="intro-title h2 case-upper mb-5"
            :text="content.whatWeDoIntroText"
          />
        </div>
        <div class="universe-launcher absolute w-full text-center" :class="{'lower': $breakpoints.landscape && $breakpoints.ltMd}">
          <CtaIcon class="cta" icon="universe" :to="{name: 'Universe'}">
            <template #labelStart>{{ $t('Discover') }}</template>
            <template #labelEnd>Universe</template>
          </CtaIcon>
        </div>
      </div>
      <div class="video z-1 sticky motion-reduce:relative motion-safe:top-0 w-full h-lview flex items-center justify-center">
        <OverlayTexts
          :list="content.whatWeDoVideoOverlayLabels"
          class="absolute inset-0 md:py-20 md:px-4"
        />
        <AspectRatio class="video-launcher" fit="contain" :ratio="$breakpoints.lg ? 16/9 : 4/5">
          <VideoLauncher
            v-if="content.whatWeDoVideo"
            class="w-full h-full"
            :order="1"
            :opacity="whatWeDoVideoOpacity"
            :video="(content.whatWeDoVideo as MediaFile)"
            :video-mobile="(content.whatWeDoVideoMobile as MediaFile)"
            :preview-media="(content.whatWeDoVideo as MediaFile)"
          />
        </AspectRatio>
      </div>
    </section>
    <SectionOurOffices :content="content" />
    <SectionAboutUs :content="content" />
    <SiteFooter />
  </div>
</template>

<style lang="scss" scoped>
.video-launcher {
  width: 90%;
  height: 100%;
  max-height: 62vh;
}

.what-we-do {
  @include motion-safe {
    @include lvh(400);
  }
}

.universe-launcher {
  @include lvh(18, 'bottom');

  @screen lg {
    @include lvh(14, 'bottom');
  }

  &.lower {
    @include lvh(12, 'bottom');
  }
}
</style>
