<script lang="ts">
import { templateRef } from '@vueuse/core'
import { gsap } from 'gsap'
import { computed, defineComponent, onMounted, onUnmounted, PropType, ref } from 'vue'

import LayerBgPlane from '@/components/atoms/LayerBgPlane.vue'
import { useBreakpoint } from '@/functions/useBreakpoint'
import { useGlobalReducedMotion } from '@/functions/useGlobalReducedMotion'
import { ProjectFragment } from '@/graphql'

import CtaPrimary from '../atoms/CtaPrimary.vue'
import ProjectWallItem from '../molecules/ProjectWallItem.vue'

export default defineComponent({
  name: 'ProjectsWall',
  components: {
    CtaPrimary,
    ProjectWallItem,
    LayerBgPlane
  },
  props: {
    list: { type: Array as PropType<ProjectFragment[]>, required: true },
    order: { type: Number, default: 0 },
    title: { type: String, required: true },
    transparent: { type: Boolean, default: true }
  },
  setup (props, { attrs, slots, emit }) {
    const bp = useBreakpoint()
    const reducedMotion = useGlobalReducedMotion()
    const wrapper = templateRef<HTMLElement>('wrapper')
    const progress = ref(0)
    let tl

    const columns = computed(() => {
      if (bp.ltMd) return 1
      if (bp.ltLg) return 2
      return 3
    })

    const strippedList = computed(() => {
      if (bp.ltMd) return props.list.slice(0, 4)
      if (bp.ltLg) return props.list.slice(0, 6)
      return props.list
    })

    const grid = computed(() => {
      const groups = new Array(columns.value).fill(undefined).map(() => [])
      strippedList.value?.forEach((item, i) => groups[i % columns.value].push(item))
      return groups
    })

    onMounted(() => {
      tl = gsap.timeline({
        scrollTrigger: {
          trigger: wrapper.value,
          start: 'top bottom',
          end: 'bottom top',
          scrub: true
        }
      })
      tl.fromTo(progress, { value: 0 }, { value: 1, ease: 'none' })
    })

    onUnmounted(() => tl?.kill())

    return {
      grid,
      progress,
      reducedMotion
    }
  }
})
</script>

<template>
  <section ref="section">
    <LayerBgPlane
      class="sticky top-0 h-lview w-full -mb-lview "
      :order="2"
      :color-mix="0"
    />
    <div class="relative pt-5 md:pt-33 container">
      <h2
        v-reveal="'revealCharText'"
        class="carret c-white case-upper mb-4 md:text-center md:mb-33"
      >
        {{ '{ '+title+' }' }}
      </h2>
      <div
        class="launcher sticky z-2 text-center pointer-events-none transition-opacity motion-reduce:mb-5"
        :class="{ 'opacity-0': progress < 0.05 || progress > 0.72 }"
      >
        <CtaPrimary green filled big class="pointer-events-auto" scrambled :to="{name: 'Works'}">See_all_projects</CtaPrimary>
      </div>
      <div ref="wrapper" class="colums relative z-1 flex gap-11 lt-md:pb-5 lt-lg:pb-20 lg:pb-30">
        <div
          v-for="(column, i) in grid"
          :key="i"
          class="w-full"
          :style="{
            transform: reducedMotion || $breakpoints.ltMd ? 'none' : `translateY(${100 * progress * (i % 2 === 0 ? -1 : 1)}px)`
          }"
        >
          <ProjectWallItem
            v-for="(item, j) in column"
            :key="j"
            :data="item"
            :order="order"
            :transparent="transparent"
          />
        </div>
      </div>
    </div>
  </section>
</template>

<style lang="scss" scoped>
h2 {
  &[data-revealed] {
    &::after {
      animation: none;
      opacity: 0!important;
    }
  }
}

.launcher {
  @include dvh(90, 'top')
}
</style>
