<script lang="ts">
import AspectRatio from '@monogrid/vue-boost/core/AspectRatio.vue'
import { computed, defineComponent, inject, PropType } from 'vue'

import { lenisInjectionKey } from '@/App.vue'
import LayerBgPlane from '@/components/atoms/LayerBgPlane.vue'
import LayerImage from '@/components/atoms/LayerImage.vue'
import LayerVideo from '@/components/atoms/LayerVideo.vue'
import { WEBGL_CONTENT_LAYER_Z } from '@/constants'
import { GetHomePageQuery } from '@/graphql'
export default defineComponent({
  name: 'SectionOurOffices',
  components: {
    AspectRatio,
    LayerBgPlane,
    LayerImage,
    LayerVideo
  },
  props: {
    content: { type: Object as PropType<GetHomePageQuery['content']>, required: true }
  },
  setup (props, { attrs, slots, emit }) {
    const lenis = inject(lenisInjectionKey)
    const enabled = computed(() => !!lenis.instance.value)
    return { enabled, WEBGL_CONTENT_LAYER_Z }
  }
})
</script>

<template>
  <section ref="el" class="our-offices relative min-h-lview flex items-center justify-center pointer-events-auto">
    <LayerBgPlane
      v-if="enabled"
      class="absolute inset-0 motion-reduce:hidden"
      :order="2"
      :color-mix="0"
    />
    <div v-if="$breakpoints.md" class="absolute inset-0">
      <AspectRatio
        v-for="media in content.ourOfficesMedia"
        :key="media.id"
        class="media absolute"
        :ratio="9/16"
        fit="contain"
      >
        <component
          :is="media.mimeType.startsWith('video') ? 'LayerVideo' : 'LayerImage'"
          class="w-full h-full"
          :transparent="true"
          :z="WEBGL_CONTENT_LAYER_Z.PLANE_ABOVE"
          :data="media"
          cover
        />
      </AspectRatio>
    </div>
    <div class="relative text-center">
      <h2
        v-if="content.ourOfficesTitle"
        v-reveal="'revealCharText'"
        class="h5 carret mb-4 c-gray"
      >
        {{ '{'+content.ourOfficesTitle+'}' }}
