<script lang="ts">
import MarkdownBlock from '@monogrid/vue-boost/core/MarkdownBlock.vue'
import { templateRef, useWindowSize } from '@vueuse/core'
import { gsap } from 'gsap'
import { MathUtils } from 'three'
import { ComponentPublicInstance, computed, defineComponent, inject, onUnmounted, PropType, ref, watch } from 'vue'

import { lenisInjectionKey } from '@/App.vue'
import CtaPrimary from '@/components/atoms/CtaPrimary.vue'
import LayerBgPlane from '@/components/atoms/LayerBgPlane.vue'
import LayerObject from '@/components/molecules/LayerObject.vue'
import { useGlobalReducedMotion } from '@/functions/useGlobalReducedMotion'
import type { GetHomePageQuery, MediaFile } from '@/graphql' // eslint-disable-line
import { fadeIn, revealText } from '@/utils/animations'
export default defineComponent({
  name: 'SectionAboutUs',
  components: {
    CtaPrimary,
    LayerBgPlane,
    MarkdownBlock,
    LayerObject
  },
  props: {
    content: { type: Object as PropType<GetHomePageQuery>, required: true }
  },
  setup (props, { attrs, slots, emit }) {
    const lenis = inject(lenisInjectionKey)
    const reducedMotion = useGlobalReducedMotion()
    const { width } = useWindowSize()
    const el = templateRef<HTMLElement>('el')
    const content1 = ref<ComponentPublicInstance<typeof MarkdownBlock>>()
    const content2 = ref<ComponentPublicInstance<typeof MarkdownBlock>>()
    const content3 = ref<HTMLElement>()
    const content4 = templateRef<HTMLElement>('content4')
    const elArm = templateRef('elArm')
    const pre1 = ref<HTMLElement>()
    const pre2 = ref<HTMLElement>()
    const pre3 = ref<HTMLElement>()
    const buttonClickable = ref(false)
    const progress = ref(0)
    const progressgradient = ref(0)
    const tls = []
    const enabled = computed(() => !!lenis.instance.value)

    watch([el, reducedMotion], ([trigger, _reducedMotion]) => {
      tls.forEach(tl => tl?.kill())
      if (!trigger || _reducedMotion) return

      const tl = gsap.timeline({
        scrollTrigger: {
          trigger,
          start: 'top top',
          end: 'bottom bottom',
          scrub: true
        }
      })

      const revealOpts = { duration: 0.1, stagger: { amount: 0.1, from: 'start' } }
      tl.fromTo(pre1.value.$el, { autoAlpha: 0 }, { autoAlpha: 1, duration: 0.1 }, 0)
      tl.add(revealText(content1.value.$el, revealOpts), 0)
      tl.fromTo(content1.value.$el, { autoAlpha: 1 }, { autoAlpha: 0, duration: 0.1 }, 0.3)
      tl.to(pre1.value.$el, { autoAlpha: 0, duration: 0.1 }, 0.3)
      tl.fromTo(pre2.value.$el, { autoAlpha: 0 }, { autoAlpha: 1, duration: 0.1 }, 0.4)
      tl.add(revealText(content2.value.$el, revealOpts), 0.4)
      tl.fromTo(content2.value.$el, { autoAlpha: 1 }, { autoAlpha: 0, duration: 0.1 }, 0.7)
      tl.to(pre2.value.$el, { autoAlpha: 0, duration: 0.1 }, 0.7)
      tl.fromTo(pre3.value.$el, { autoAlpha: 0 }, { autoAlpha: 1, duration: 0.1 }, 0.8)
      tl.add(revealText(content3.value.$el, revealOpts), 0.8)
      tl.add(fadeIn(content4.value.$el, {
        duration: 0.1,
        onReverseComplete: () => (buttonClickable.value = false),
        onStart: () => (buttonClickable.value = true)
      }),
      1)
      tls.push(tl)

      const tl2 = gsap.timeline({
        scrollTrigger: {
          trigger,
          start: 'top bottom',
          end: 'bottom bottom',
          scrub: true
        }
      })
      tl2.fromTo(progress, { value: 0.2 }, { value: 0.7, duration: 1, ease: 'linear' })
      tl2.fromTo(progressgradient, { value: 1 }, { value: 3, duration: 0.5, ease: 'power2.out' }, 0.5)
      tls.push(tl2)
    })

    onUnmounted(() => tls.forEach(tl => tl?.kill()))

    watch([() => elArm?.value?.obj, width], ([obj, w]) => {
      if (!obj) return
      obj.rotation.y = MathUtils.degToRad(-180)
      obj.domMapping.transform.translate.x = w * -15 / 100
    }, { immediate: true })

    return {
      pre1,
      pre2,
      pre3,
      content1,
      content2,
      content3,
      enabled,
      progress,
      progressgradient,
      reducedMotion,
      buttonClickable
    }
  }
})
</script>

<template>
  <section ref="el" class="pointer-events-none about-us relative">
    <LayerObject
      ref="elArm"
      class="z-0"
      :class="{
        'absolute inset-0 bottom-20': enabled,
        'sticky top-0 h-lview -mb-lview': !enabled,
      }"
      :data="(content.aboutUsGlb as MediaFile)"
      :wrap="true"
      :order="1"
      :transparent="true"
      :animation-progress="progress"
      :fallback="content.aboutUsBackground"
    />
    <div class="sticky h-lview top-0 z-2">
      <LayerBgPlane
        v-if="enabled"
        class="absolute inset-0 motion-reduce:hidden"
        :order="0"
        :color-mix="0"
        :transparent="true"
      />
      <div class="absolute inset-0">
        <div class="container pt-safe-header pb-16 lg:pb-60 !lg:pl-35 h-full flex flex-col justify-end lg:items-start lg:justify-center">
          <MarkdownBlock
            ref="pre1"
            tag="p"
            inline
            class="h6 c-grey mb-4"
            html
            :text="`{&nbsp;&nbsp;${content.aboutUsPreText1}&nbsp;&nbsp;}`"
          />
          <MarkdownBlock
            ref="content1"
            tag="h2"
            inline
            class="h4 lg:w-[45%]"
            :text="content.aboutUsText1"
          />
        </div>
      </div>
      <div class="absolute inset-0 lg:flex lg:flex-col lg:items-end">
        <div class="container pt-safe-header pb-16 lg:pb-20 lg:w-[50%] h-full flex flex-col justify-end lg:items-start lg:justify-center">
          <MarkdownBlock
            ref="pre2"
            tag="p"
            inline
            class="h6 c-grey mb-4"
            html
            :text="`{&nbsp;&nbsp;${content.aboutUsPreText2}&nbsp;&nbsp;}`"
          />
          <MarkdownBlock
            ref="content2"
            tag="h2"
            inline
            class="h4 w-[85%]"
            :text="content.aboutUsText2"
          />
        </div>
      </div>
      <div class="absolute inset-0">
        <div class="container pt-safe-header pb-16 !lg:pb-5 !lg:pt-60 !lg:pl-35 h-full flex flex-col justify-end lg:items-start lg:justify-center">
          <MarkdownBlock
            ref="pre3"
            tag="p"
            inline
            class="h6 c-grey mb-4"
            html
            :text="`{&nbsp;&nbsp;${content.aboutUsPreText3}&nbsp;&nbsp;}`"
          />
          <div class="lg:w-[45%]">
            <MarkdownBlock
              ref="content3"
              tag="p"
              class="h4"
              :text="content.aboutUsText3"
              inline
            />
            <CtaPrimary
              ref="content4"
              class="mt-4"
              :class="{'pointer-events-auto': buttonClickable || reducedMotion}"
              green
              :to="{name: 'About'}"
            >
              {{ content.aboutUsCtaLabel }}
            </CtaPrimary>
          </div>
        </div>
      </div>
      <slot />
    </div>
    <div class="gradient absolute left-0 bottom-0 z-0 w-full h-[85lvh]" />
  </section>
</template>

<style lang="scss" scoped>
section {
  @include lvh(300,'height')
}

.gradient {
  background: linear-gradient(0deg, rgba(0,0,0,100%) 10%, rgba(0,0,0,0%) 100%);

  @screen lg {
    transform: scaleY(v-bind(progressgradient));
    transform-origin: bottom left;
  }
}
</style>
