<script lang="ts">
import { gsap } from 'gsap'
import { defineComponent, PropType, ref } from 'vue'

import LayerImage from '@/components/atoms/LayerImage.vue'
import { WEBGL_CONTENT_LAYER_Z } from '@/constants'
import { ProjectFragment } from '@/graphql'

import IconComponent from '../atoms/IconComponent.vue'
import LabelText from '../atoms/LabelText.vue'

export default defineComponent({
  name: 'ProjectWallItem',
  components: {
    LayerImage,
    LabelText,
    IconComponent
  },
  props: {
    order: { type: Number, default: 0 },
    transparent: { type: Boolean, default: true },
    data: { type: Object as PropType<ProjectFragment>, required: true }
  },
  setup (props, { attrs, slots, emit }) {
    const hoverEffect = ref(0)
    const scaleEffect = ref(1)
    const tl = gsap.timeline({ paused: true })

    tl.fromTo(hoverEffect, { value: 0 }, { value: 1, duration: 1, ease: 'none' })
    tl.fromTo(scaleEffect, { value: 1 }, { value: 1.05, duration: 1, ease: 'none' }, '<')

    return {
      WEBGL_CONTENT_LAYER_Z,
      hoverEffect,
      scaleEffect,
      onEnter: () => tl.play(),
      onLeave: () => tl.reverse()
    }
  }
})
</script>

<template>
  <RouterLink
    :to="{name: 'Project', params: {slug: data._slug}}"
    class="item block relative w-full c-grey mb-6 lt-lg:pb-8 lg:mb-18"
    @mouseenter="onEnter"
    @mouseleave="onLeave"
  >
    <LayerImage
      :auto-reveal="false"
      :order="order"
      :transparent="transparent"
      :data="data.heroImage"
      cover
      :z="WEBGL_CONTENT_LAYER_Z.PLANE_ABOVE"
      :hover="hoverEffect"
      :scale="scaleEffect"
      :options="{'max-w' : 600}"
      class="w-full image mb-6"
    />
    <div>
      <div class="mb-4 flex gap-1">
        <LabelText
          v-for="category in data.categories"
          :key="category.id"
          primary
        >
          {{ category.title }}
        </LabelText>
      </div>
      <h1 class="h5 mb-3">{{ data.title }}</h1>
      <div v-if="data.excerpt" class="flex items-end">
        <div class="flex-auto mr-4">
          <p>{{ data.excerpt }}</p>
        </div>
        <IconComponent class="c-green w-3 h-3 flex-shrink-0" icon="arrowTopRight" />
      </div>
    </div>
    <div v-if="$breakpoints.ltMd" v-reveal.repeat="'scaleX'" v-reveal:options="{ duration: 1, delay: 0.3 }" class="project-border border-horizontal origin-left bg-grey-dark bottom-0" />
  </RouterLink>
</template>

<style lang="scss" scoped>

.item {
  @include hover {
    transition: color var(--transition-duration), box-shadow var(--transition-duration);

    &:hover {
      color: color('white');
      text-shadow: rem(0px 0px 4px) rgba(color('white'), 0.6);
    }
  }

  &:nth-last-of-type(1) .project-border {
    display: none;
  }
}

.image {
  aspect-ratio: 4/3;
}
</style>
